body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-header {
  /*background-color: #282c34;*/
  background-color: #174c80;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

.navbar {
  background-color: #174c80;
}

.navbar-item {
  color: #fff;
}

.results--section {
  padding: 20px 0px;
  margin-top: 40px;
}

.history--section {
  padding: 20px 0px;
  margin-top: 40px;
}


h1 {
  text-align: center;
  font-size: 40px;
}

h2 {
  font-size: 35px;
  color:#0667D0;
}

.today--section {
    margin-bottom: 40px;
    padding: 0 50px;
}
.today--section h2 {
    font-size: 20px;
}
.today--section__box {
    background-color: white;
    padding: 20px;
    margin: 20px 0;
    border-radius: 4px;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.);
}
.btc--section {
    text-align: center;
    border-right: 1px solid #DAE1E8;
}
.btc--section h5 {
    font-size: 30px;
}
.icx--section {
    text-align: center;
    border-right: 1px solid #DAE1E8;
}
.icx--section h5 {
    font-size: 30px;
}
.nano--section {
    text-align: center;
}
.nano--section h5 {
    font-size: 30px;
}
@media (max-width: 480px) {
    .icx--section {
        border-right: none;
    }
    .btc--section {
        border-right: none;
    }
    .today--section {
        margin-top:  50px;
    }
}
.history--section {
    margin-bottom: 40px;
    padding: 50px;
}
.history--section h2 {
    font-size: 20px;
}
.history--section__box {
    background-color: white;
    padding: 20px;
    margin: 20px 0;
    border-radius: 4px;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}
.history--section__box__inner {
    display: block;
    padding: 20px;
    width: 100%;
    border-bottom: 1px dashed #7D95B6;
}
.history--section__box__inner:nth-last-child(1) {
    border-bottom: none;
}
.history--section__box__inner h4 {
    color: #0667D0;
    margin-bottom: 10px;
}
.history--section__box__inner p {
    text-align: center;
}
@media (max-width: 480px) {
    .history--section__box__inner h4 {
        text-align: center;
    }
}
